body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0px;
}

.nav-title {
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    color: #666;
    padding-left: 10px;
}

.nav {
  overflow: hidden;
  /* background-color: #00acb0; */
}

.nav-link {
  float: right;
  color: #666;
  /* background-color: #00acb0; */
  text-align: center;
  padding: 24px 10px;
  text-decoration: none;
  font-size: 15px;

  /* these last two make the logout button in
  the nav bar look like another link */
  border:none;
  cursor: pointer;
  outline:0;
}

.nav-link:hover, .nav-title:hover {
  color: #008183;
}

.nav-right {
  float: right;
}